import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connectRaffle, updateAccount } from "../redux/blockchain/blockchainActionsRaffle";
import { fetchData } from "../redux/data/dataActionsRaffle";
import * as s from "../styles/globalStyles";
import styled from "styled-components";
import Web3 from "web3";


const truncate = (input, len) =>
  input.length > len ? `${input.substring(0, len)}...` : input;

export const StyledButton = styled.button`
  padding: 10px;
  border-radius: 50px;
  border: none;
  background-color: var(--secondary);
  padding: 10px;
  font-weight: bold;
  color: var(--secondary-text);
  width: 100px;
  cursor: pointer;
  box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const StyledRoundButton = styled.button`
  padding: 10px;
  border-radius: 100%;
  border: none;
  background-color: var(--secondary);
  padding: 10px;
  font-weight: bold;
  font-size: 15px;
  color: var(--secondary-text);
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const StyledLogo = styled.img`
  width: 275px;
  @media (min-width: 767px) {
    width: 600px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
  
`;

export const StyledImg = styled.img`
  box-shadow: 0px 5px 11px 2px rgba(0, 0, 0, 0.7);
  border: 4px dashed var(--secondary);
  background-color: var(--accent);
  border-radius: 100%;
  width: 200px;
  @media (min-width: 900px) {
    width: 250px;
  }
  @media (min-width: 1000px) {
    width: 300px;
  }
  transition: width 0.5s;
`;

export const StyledLink = styled.a`
  color: var(--secondary);
  text-decoration: none;
`;

export const MintPageLink = styled.a`
  color: var(--secondary);
  text-decoration: none;
`;

function Raffles() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingRaffle, setClaimingRaffle] = useState(false);
  const [info, setInfo] = useState('');
  const [feedback, setFeedback] = useState(`Each Toker grants you access to two tickets.`);
  const [mintAmount, setMintAmount] = useState(1);
  const [CONFIG, SET_CONFIG] = useState({
    RAFFLE_CONTRACT_ADDRESS: "",
    RAFFLE_SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    RAFFLE_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    TICKET_WEI_COST: 0,
    TICKET_DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MINT_PAGE: "",
    PROJECT_LINK: "",
    SHOW_BACKGROUND: false,
  });

  const [RAFFLE, SET_RAFFLE] = useState({
  PRIZE: "",
  LIVE: "",
  });
  


  const claimRaffle = () => {
    let cost = CONFIG.TICKET_WEI_COST;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);
    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`Grabbin' yo tickets!`);
    setClaimingRaffle(true);
    blockchain.smartContract.methods
      .enterRaffle(mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        gasLimit: String(totalGasLimit),
        maxPriorityFeePerGas: null,
        maxFeePerGas: null,
        to: CONFIG.RAFFLE_CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      // if error
      .once("error", (err) => {
        console.log(err);
        setFeedback(err.message);
        setClaimingRaffle(false);
      })
      // if success
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `Your tickets have been purchased! Refresh the page if your tickets don't show.`
        );

        //return state to "re-buy"
        setClaimingRaffle(false);

        //once bought, update playerTickets
        dispatch(fetchData(blockchain.account));
      });
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > 25) {
      newMintAmount = 25;
    }
    setMintAmount(newMintAmount);
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };



  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();
  }, []);

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  const arr = [data.getPlayerTickets];
  const tickets = arr.join(",");

  const winningTickets = [data.getWinners];
  const winners = winningTickets.join(",");
  
  const totalTokers = [data.tokenCount];
  const tokers = totalTokers.join(",")

  const addy = (ethereum.selectedAddress)
  const numWinners = (data.numWinners)
  const rafflePrize = (RAFFLE.PRIZE)
  const numTickets = (data.getNumTickets)
  const currentEntries = (data.currentEntries)

  console.log({tickets});
  console.log({winners});
  console.log({addy});
  console.log({numWinners});
  console.log({rafflePrize});
  console.log({numTickets});
  console.log({currentEntries});


  const getPrizes = async () => {
  const prizeResponse = await fetch("/config/rewards.json");

    const prizes = await prizeResponse.json();
    SET_RAFFLE(prizes);
  };

  useEffect(() => {
    getPrizes();
  }, []);




  return (
    <s.Screen>
      
      <s.Container
        flex={1}
        ai={"center"}
        style={{ padding: 10, backgroundColor: "var(--primary)" }}
        image={CONFIG.SHOW_BACKGROUND ? "/config/images/bg_2.png" : null} 
      >
      
        <ResponsiveWrapper flex={1} style={{ padding: 18 }} test>
          <s.Container
            flex={2}
            jc={"center"}
            ai={"center"}
            style={{
              backgroundColor: "var(--accent)",
              padding: 18,
              borderRadius: 50,
              //border: "4px double var(--secondary)",
              boxShadow: "0px 5px 11px 2px rgba(0,0,0,0.7)",
            }}
          >
                    <StyledButton
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                      }}
                    >
                      ENTER
                    </StyledButton>
                    <s.SpacerLarge />

            <s.TextDescription
              style={{
                fontSize: 55,
                fontFamily: "Bangers",
                textAlign: "center",
                color: "var(--primary-text)",
              }}
            >
              
        </s.TextDescription>

           <s.TextDescription
              style={{
                textAlign: "center",
                color: "var(--primary-text)",
                }}
            >
              Contract Address:<br />
              <StyledLink target={"_blank"} href={CONFIG.RAFFLE_SCAN_LINK}>
                {truncate(CONFIG.RAFFLE_CONTRACT_ADDRESS, 15)}
              </StyledLink>
              
            </s.TextDescription>
            
            <s.TextTitle
              style={{
                textAlign: "center",
                fontSize: 25,
                fontWeight: "bold",
                color: "var(--accent-text)",
              }}
            >
              
            </s.TextTitle>
            
            <s.SpacerSmall />
            {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  OH SNAP.. Something Went Wrong.
                </s.TextTitle>
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  ...Raffles might not be active.
                </s.TextDescription>
                <s.SpacerSmall />
              </>
            ) : (
              <>
              
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  {info}
                  
                  You must have at least one Midnight Toker in your wallet to participate.
                  <br />
                  If you do not have any please visit the 
                  <MintPageLink a href="https://www.themidnighttokersclub.com/index.html"> Mint Page
                  </MintPageLink>
                  <br />
                  </s.TextTitle>
                  <s.TextTitle
                  style={{ textAlign: "center", color: "var(--sales-text)" }}
                >
                  1 {CONFIG.TICKET} costs {CONFIG.TICKET_DISPLAY_COST}{" "}
                  {CONFIG.NETWORK.SYMBOL}.
                  </s.TextTitle>
                
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  Excluding gas fees.
                </s.TextDescription>
                <s.SpacerSmall />
                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      Connect to the {CONFIG.NETWORK.NAME} network
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <StyledButton
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connectRaffle());
                        getData();
                      }}
                    >
                      CONNECT
                    </StyledButton>


                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--accent-text)",
                          }}
                        >
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  <>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                     
                      {feedback}
                      <br />

                      You can buy a maximum of {data.getMaxEntries} tickets. <br />
                      <br />

                      Is Raffle Active? {RAFFLE.LIVE} <br />
                      Raffle Prize is: { RAFFLE.PRIZE} <br />
                      <br />
                      You own { data.currentEntries} out of { data.getNumTickets} tickets. <br />
                      Your Tickets: { tickets} <br />
                      Winning Tickets: { winners} <br />
                      Total Winners: { data.numWinners} <br />
                    </s.TextDescription>
                    
                    <s.SpacerMedium />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledRoundButton
                        style={{ lineHeight: 0.4 }}
                        disabled={claimingRaffle ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          decrementMintAmount();
                        }}
                      >
                        -
                      </StyledRoundButton>
                      <s.SpacerMedium />
                      <s.TextDescription
                        style={{
                          textAlign: "center",
                          color: "var(--accent-text)",
                        }}
                      >
                        {mintAmount}
                      </s.TextDescription>
                      <s.SpacerMedium />
                      <StyledRoundButton
                        disabled={claimingRaffle ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          incrementMintAmount();
                        }}
                      >
                        +
                      </StyledRoundButton>
                    </s.Container>
                    <s.SpacerSmall />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledButton
                        disabled={claimingRaffle ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          claimRaffle();
                          getData();
                        }}
                      >
                        {claimingRaffle ? "BUSY" : "BUY"}
                      </StyledButton>

                    </s.Container>
                  </>
                )}
              </>
            )} 
          </s.Container>
        </ResponsiveWrapper>
      </s.Container>
    </s.Screen>
  );
}

export default Raffles;
